<template>
  <div class="Bets-Item state-0" @click="open">
    <div class="top">
      <div><img :src="item.icon" width="20" alt=""><span>{{ item.season_match_name }}</span></div>
      <div><img :src="item.guest_team_logo" width="26" alt=""></div>
      <div><img :src="item.host_team_logo" width="26" alt=""></div>
    </div>
    <div class="center">
      <p><span>{{ item.game_start_time|dateFormat('hh:mm') }}</span><span>BO1</span></p>
      <div><span>{{ item.guest_team_name }}</span></div>
      <div><span>{{ item.host_team_name }}</span></div>
    </div>
    <div class="bottom">
      <div class="activation"><span></span></div>
      <div><span>{{ odds('v0').v0 }}</span></div>
      <div><span>{{ odds('v3').v3 }}</span></div>
    </div>
  </div>
</template>

<script>//
import { token } from '../../tool/storeManage'
import { Toast } from 'mint-ui'

export default {
  name: 'LotteryBetsItem2',
  props: {
    item: { type: Object }
  },
  methods: {
    odds (type) {
      if (this.item.odd) {
        const item = this.item.odd.find(i => {
          return i[type] !== undefined
        })
        if (item === -1) return null
        return item
      }
      return null
    },
    open () {
      if (!token.Token) {
        Toast('请登陆')
        this.$router.replace({
          path: '/login'
        })
        return
      }
      this.$router.push({
        path: '/webPage',
        query: { url: this.item.link, title: '365彩票', showHead: 'false', post: true }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  $border: 4px;
  .item {
    height: 36px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
  }

  .item2 {
    border-top: 1px solid #494949;
  }

  .Bets-Item {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin-top: 5px;
    > div {
      width: 33.3%;
    }
    > div.top {
      > div:nth-child(1) {
        height: 26px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        background-color: #1C1C1C;
        border-top-left-radius: $border;
        padding-left: 5px;
        span {
          margin-left: 5px;
          flex: 1;
          color: #999;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      > div:nth-child(3) {
        border-bottom-left-radius: $border;
        @extend .item2;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background-color: #313131;
      }
    }
    > div.center {
      > p {
        height: 26px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-around;
        background-color: #1C1C1C;
        span {
          color: #999;
        }
      }
      > div:nth-child(3) {
        @extend .item2;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background-color: #313131;
      }
    }
    > div.bottom {
      > div:nth-child(1) {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        height: 26px;
        background-color: #1C1C1C;
        border-top-right-radius: $border;
        color: #FFC63A;
        position: relative;
      }
      > div:nth-child(1).activation::after {
        content: '';
        display: inline-block;
        position: absolute;
        right: 0;
        width: 35px;
        height: 35px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("../../assets/competition/ic_guss@2x.png");
      }
      > div:nth-child(3) {
        @extend .item2;
        border-bottom-right-radius: $border;
      }
      > div:nth-child(2), > div:nth-child(3) {
        @extend .item;
        background: linear-gradient(180deg, rgba(63, 63, 63, 1) 0%, rgba(49, 49, 49, 1) 100%);
        color: #FFC63A;
      }
      > div:nth-child(2):active, > div:nth-child(3):active {
        background: linear-gradient(0deg, rgba(153, 153, 153, 1) 0%, rgba(204, 204, 204, 1) 100%);
        color: #131313;
      }
    }
  }

  .state-1 {
    > div.bottom {
      > div:nth-child(2), > div:nth-child(3) {
        color: #999;
      }
    }
  }

  .state-2, .state-3 {
    > div.bottom {
      > div:nth-child(1) {
        color: #999;
      }
      > div:nth-child(2), > div:nth-child(3) {
        color: #999;
      }
    }
  }
</style>
